* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.container {
  width: 100vw;
  height: 100vh;
background-image: linear-gradient(to top right, #C107B2 0%, #4700B1 70%);
  background-size: cover;
  background-repeat: no-repeat;

}

.welcomeBox {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  margin: 0 0 0 0;
  height: 470px;
  width: 470px;
  border-radius: 15px;
  padding: 75px 40px 55px 40px;
}

.welcomeBox-header {
  text-align: center;
  padding: 0px 10px;
  font-weight: 300;
  font-size: 25px;
  color: #3b3b3b;
  span {
    font-weight: bold;
  }
}

.welcomeBox-pictures {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px 50px 20px 50px;
}

.welcomeBox-pictures-1,
.welcomeBox-pictures-2,
.welcomeBox-pictures-3 {
  height: 100px;
  width: 100px;
  background-image: url("./../images/left_image.png");
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  &:hover {
    z-index: 3;
    cursor: pointer;
      box-shadow: 0px 0px 20px 0px #aaaaaa;
  }

}

.welcomeBox-pictures-1 {
  position: absolute;
  left: 100px;
}

.welcomeBox-pictures-2 {
  height: 135px;
  width: 135px;
  background-image: url("./../images/middle_image.png");
  box-shadow: 0px 0px 20px 0px #aaaaaa;
  z-index: 2;
}

.welcomeBox-pictures-3 {
  background-image: url("./../images/right_image.png");
  position: absolute;
  right: 100px;
}

.welcomeBox-form {

}

.welcomeBox-form-email {
  position: relative;
  display: flex;
  border-bottom: 2px solid #cecece;
  margin-bottom: 20px;

  &::before {
    content: "";
    position: absolute;
    top: 11px;
    left: 5px;
    display: block;
    background-image: url(./../images/envelope.png);
    background-size: cover;
    height: 20px;
    width: 20px;
  }
}

.welcomeBox-form-email-input {
  width: 100%;
  padding: 8px 0 8px 35px;
  font-size: 20px;
  border: none;
}

.welcomeBox-form-btn {
  display: block;
  height: 55px;
  width: 385px;
  border: none;
  color: #fff;
  border-radius: 10px;
  background-image: linear-gradient(to top right, rgb(159, 6, 173) 0%, rgb(208, 37, 214) 100%);
  font-weight: 700;
  font-size: 20px;
  box-shadow: 0px 0px 10px 0px #666666;
  cursor: pointer;
  transition: 0.2s all;
  &:before{
    content: "";
    border-top: 1px solid#4700b1;
    border-left: 1px solid#4700b1;
    height: 0px;
    width:0px ;
    position: absolute;

  }
  &:hover {
  box-shadow: 0px 0px 15px 2px #666666;

.welcomeBox-form-btn:before {
      height: 55px;
      widows: 385px;
    }
  }
}